/**
 * AdjacencyPersonalizationView (Updated for Phase 5D)
 * 
 * Now uses AppContext for persistence to IONOS backend.
 * Decisions are saved in real-time as user makes selections.
 * Loads previously saved decisions on mount.
 * 
 * Data Flow:
 * - Load: fyiData.mvpAdjacencyConfig.decisionAnswers  AdjacencyPersonalization
 * - Save: AdjacencyPersonalization  updateMVPAdjacencyConfig  fyiData  IONOS
 */

import React, { useMemo, useEffect, useCallback, useContext } from 'react';
import { 
  ArrowLeft, 
  AlertTriangle, 
  CheckCircle,
  XCircle,
  Info
} from 'lucide-react';

// Import context
import AppContext from '../../contexts/AppContext';

// Import the hook for KYC data
import { useKYCData } from '../../hooks/useKYCData';

// Import from mansion-program TypeScript module
import { 
  AdjacencyPersonalization,
  getPreset
} from '../../mansion-program';

/**
 * KYC Completeness Banner
 * Shows when KYC data is incomplete
 */
function KYCCompletenessCheck({ completeness, onGoToKYC }) { // eslint-disable-line no-unused-vars
  const { percentage, hasMinimumData, missingRequired } = completeness;

  if (hasMinimumData) {
    return null; // Don't show if we have minimum data
  }

  return (
    <div className="bg-amber-50 border border-amber-200 rounded-lg p-4 mb-6">
      <div className="flex items-start gap-3">
        <AlertTriangle className="w-5 h-5 text-amber-600 flex-shrink-0 mt-0.5" />
        <div className="flex-1">
          <h3 className="font-medium text-amber-800">
            Complete Your Profile for Better Recommendations
          </h3>
          <p className="text-sm text-amber-700 mt-1">
            The personalization engine works best with more information about your lifestyle.
            Currently {percentage}% complete.
          </p>
          
          {missingRequired.length > 0 && (
            <div className="mt-3">
              <p className="text-sm font-medium text-amber-800">Missing required sections:</p>
              <ul className="mt-1 text-sm text-amber-700">
                {missingRequired.map(section => (
                  <li key={section} className="flex items-center gap-2">
                    <XCircle className="w-3 h-3" />
                    {formatSectionName(section)}
                  </li>
                ))}
              </ul>
            </div>
          )}
          
          <button
            onClick={onGoToKYC}
            className="n4s-btn n4s-btn--primary mt-3"
          >
            Complete KYC Profile
          </button>
        </div>
      </div>
    </div>
  );
}

/**
 * Progress indicator showing KYC sections
 */
function KYCProgressIndicator({ completeness }) {
  const { sections, percentage } = completeness;
  
  return (
    <div className="bg-gray-50 rounded-lg p-4 mb-6">
      <div className="flex items-center justify-between mb-2">
        <span className="text-sm font-medium text-gray-700">Profile Completeness</span>
        <span className="text-sm text-gray-500">{percentage}%</span>
      </div>
      
      <div className="w-full bg-gray-200 rounded-full h-2 mb-3">
        <div 
          className="h-2 rounded-full transition-all duration-300"
          style={{ width: `${percentage}%`, background: '#1e3a5f' }}
        />
      </div>
      
      <div className="grid grid-cols-3 gap-2 text-xs">
        {Object.entries(sections).map(([key, complete]) => (
          <div 
            key={key}
            className={`flex items-center gap-1 ${complete ? 'text-green-600' : 'text-gray-400'}`}
          >
            {complete ? (
              <CheckCircle className="w-3 h-3" />
            ) : (
              <div className="w-3 h-3 rounded-full border border-current" />
            )}
            <span>{formatSectionName(key)}</span>
          </div>
        ))}
      </div>
    </div>
  );
}

/**
 * Format section key to display name
 */
function formatSectionName(key) {
  const names = {
    familyHousehold: 'Family & Household',
    lifestyleLiving: 'Lifestyle',
    workingPreferences: 'Work Preferences',
    projectParameters: 'Project Parameters',
    spaceRequirements: 'Space Requirements',
    designIdentity: 'Design Identity',
    budgetFramework: 'Budget',
    culturalContext: 'Cultural Context',
    portfolioContext: 'Portfolio'
  };
  return names[key] || key;
}

/**
 * Main Component
 */
export default function AdjacencyPersonalizationView({ 
  onBack,
  onComplete,
  onViewDiagram,
  onGoToKYC,
  showProgress = true
}) {
  // Get context for persistence
  const { 
    fyiData, 
    updateMVPAdjacencyConfig,
    updateMVPDecisionAnswer 
  } = useContext(AppContext);
  
  // Use the hook to get KYC data
  const {
    kycResponse,
    preset,
    baseSF,
    completeness
  } = useKYCData();
  
  // Get saved decisions from context
  const savedDecisions = fyiData?.mvpAdjacencyConfig?.decisionAnswers || {};
  const savedTier = fyiData?.mvpAdjacencyConfig?.tier;
  
  // Get base preset data
  const presetData = useMemo(() => {
    try {
      return getPreset(preset);
    } catch (e) {
      console.error('Failed to get preset:', e);
      return null;
    }
  }, [preset]);
  
  const baseMatrix = presetData?.adjacencyMatrix || [];
  
  // Update tier in context when it changes (from FYI settings)
  useEffect(() => {
    if (preset && preset !== savedTier) {
      console.log('[AdjacencyView] Updating tier:', preset);
      updateMVPAdjacencyConfig({ tier: preset });
    }
  }, [preset, savedTier, updateMVPAdjacencyConfig]);
  
  // Handle decision change - save to context immediately
  const handleDecisionChange = useCallback((decisionId, optionId) => {
    console.log('[AdjacencyView] Decision changed:', decisionId, optionId);
    updateMVPDecisionAnswer(decisionId, optionId);
  }, [updateMVPDecisionAnswer]);
  
  // Handle completion - save final state and timestamp
  const handleComplete = useCallback((result) => {
    console.log('[AdjacencyView] Personalization complete:', result);
    
    // Save completion timestamp and all choices
    const decisionAnswers = {};
    result.choices.forEach(choice => {
      decisionAnswers[choice.decisionId] = choice.selectedOptionId;
    });
    
    updateMVPAdjacencyConfig({
      tier: preset,
      decisionAnswers,
      questionnaireCompletedAt: new Date().toISOString(),
    });
    
    if (onComplete) {
      onComplete(result);
    }
  }, [preset, updateMVPAdjacencyConfig, onComplete]);
  
  // If preset data failed, show error
  if (!presetData) {
    return (
      <div className="p-6">
        <button 
          onClick={onBack}
          className="n4s-btn n4s-btn--ghost mb-4"
        >
          <ArrowLeft className="w-4 h-4" />
          Back to Overview
        </button>
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <h3 className="text-red-800 font-medium">Unable to load Personalization</h3>
          <p className="text-red-600 text-sm mt-1">
            Please ensure property configuration is complete.
          </p>
        </div>
      </div>
    );
  }
  
  // If minimum data not present, show completeness check prominently
  if (!completeness.hasMinimumData) {
    return (
      <div className="p-6 max-w-2xl mx-auto">
        <button 
          onClick={onBack}
          className="n4s-btn n4s-btn--ghost mb-6"
        >
          <ArrowLeft className="w-4 h-4" />
          Back to Overview
        </button>
        
        <div className="bg-white rounded-xl shadow-lg p-8">
          <div className="text-center mb-6">
            <div className="w-16 h-16 bg-amber-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <Info className="w-8 h-8 text-amber-600" />
            </div>
            <h2 className="text-xl font-bold text-gray-900">
              Complete Your Profile First
            </h2>
            <p className="text-gray-600 mt-2">
              We need some basic information about your household and lifestyle 
              to provide personalized layout recommendations.
            </p>
          </div>
          
          <KYCProgressIndicator completeness={completeness} />
          
          <div className="bg-amber-50 border border-amber-200 rounded-lg p-4 mb-6">
            <h4 className="font-medium text-amber-800 mb-2">Required sections:</h4>
            <ul className="space-y-2">
              {completeness.missingRequired.map(section => (
                <li key={section} className="flex items-center gap-2 text-amber-700">
                  <XCircle className="w-4 h-4" />
                  {formatSectionName(section)}
                </li>
              ))}
            </ul>
          </div>
          
          <div className="flex gap-4">
            <button
              onClick={onGoToKYC}
              className="n4s-btn n4s-btn--primary flex-1"
            >
              Go to KYC Profile
            </button>
            <button
              onClick={onBack}
              className="n4s-btn n4s-btn--secondary"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    );
  }
  
  // Render the personalization UI
  return (
    <div className="min-h-screen bg-gray-50">
      {/* Optional progress indicator at top */}
      {showProgress && completeness.percentage < 100 && (
        <div className="bg-white border-b px-6 py-3">
          <div className="max-w-4xl mx-auto">
            <div className="flex items-center justify-between text-sm">
              <span className="text-gray-600">
                Profile {completeness.percentage}% complete
              </span>
              <button
                onClick={onGoToKYC}
                className="text-navy hover:opacity-80"
                style={{ color: '#1e3a5f' }}
              >
                Improve recommendations 
              </button>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-1 mt-2">
              <div 
                className="h-1 rounded-full"
                style={{ width: `${completeness.percentage}%`, background: '#1e3a5f' }}
              />
            </div>
          </div>
        </div>
      )}
      
      <AdjacencyPersonalization
        kyc={kycResponse}
        preset={preset}
        baseSF={baseSF}
        baseMatrix={baseMatrix}
        savedDecisions={savedDecisions}
        onDecisionChange={handleDecisionChange}
        onComplete={handleComplete}
        onCancel={onBack}
        onViewDiagram={onViewDiagram}
      />
    </div>
  );
}
